<template>
  <section>
    <div class="level">
      <div class="level-left"></div>
      <div class="level-right">
        <div class="level-item">
          <b-button size="is-small" type="is-cgreen-4" @click="saveToHistory()">保存至历史记录</b-button>
        </div>
      </div>
    </div>
    <div v-if="localdata !== undefined">
      <vue-json-pretty :data="localdata" :deep="4" :virtual="true" />
    </div>
  </section>
</template>

<script>
import VueJsonPretty from 'vue-json-pretty'
import 'vue-json-pretty/lib/styles.css'
export default {
  name: 'RelCheck',
  components: {
    VueJsonPretty
  },
  props: {},
  data() {
    return {
      localdata: {}
    }
  },
  computed: {
    returnJsonObj() {
      const str = JSON.stringify(this.localdata)
      return JSON.parse(str)
    }
  },

  methods: {
    /**
     * 获取数据
     */
    setData() {
      this.localdata = this.$store.state.releasemanagement.activeRelease
    },
    /**
     * 保存到历史归档
     */
    saveToHistory() {
      this.$store.commit('uploadHistoryRelease', this.localdata) // 提交
      this.$buefy.toast.open({
        message: '本次释放已保存至历史归档!',
        type: 'is-success'
      })
    }
  }
}
</script>

<style scoped>
.padding-05 {
  padding: 0.5rem;
}
</style>
