<template>
  <div>
    <title-bar :title-stack="titleStack" />
    <section class="section is-main-section">
      <div class="columns">
        <div class="column is-1 has-background-cwhite-2">
          <div class="buttons">
            <b-button type="is-cgreen-4" size="is-small" @click="createNewRelTask()">新建</b-button>
            <b-button type="is-cblue-4" size="is-small" @click="importRelTask()">导入</b-button>
          </div>
          <b-button expanded size="is-small" type="is-cblue-1" @click="openActive()">
            {{ activerelease.taskname }}
          </b-button>
        </div>
        <div class="column">
          <div v-if="defaultshow === true">
            <release-entrance ref="releaseentrance"></release-entrance>
          </div>
        </div>
      </div>
    </section>
    <div>
      <b-modal v-model="showmodal.isshow" :has-modal-card="true" :can-cancel="false" @close="closeModal">
        <template #default="props">
          <b-field label="输入Task名称" :type="returnInput.itype" :message="returnInput.imessage">
            <b-input v-model="showmodal.name" maxlength="20" />
          </b-field>
          <b-field label="输入任务执行人" :type="returnInput.itype" :message="returnInput.imessage">
            <b-input v-model="showmodal.assigner" maxlength="20" />
          </b-field>
          <b-field label="输入任务创建人" :type="returnInput.itype" :message="returnInput.imessage">
            <b-input v-model="showmodal.createautor" maxlength="20" />
          </b-field>
          <b-button expanded size="is-small" type="is-cyellow-1" @click="props.close()">保存</b-button>
        </template>
      </b-modal>
    </div>
  </div>
</template>

<script>
import TitleBar from '@/components/TitleBar'
import ReleaseEntrance from '@/components/ReleaseManagement/ReleaseEntrance'
export default {
  /**
   * 当前释放的
   */
  name: 'NowSprint',
  components: {
    TitleBar,
    ReleaseEntrance
  },
  data() {
    return {
      /**
       * 是否默认展示
       */
      defaultshow: false,

      /**
       * modal
       */
      showmodal: {
        isshow: false,
        /**
         * 释放task名称
         * 名称
         */
        name: '',
        /**
         * 执行人
         */
        assigner: '',
        /**
         * 创建人
         */
        createautor: ''
      },
      activerelease: {}
    }
  },
  computed: {
    titleStack() {
      return ['RoWalock', '当前释放版本']
    },
    returnInput() {
      if (this.showmodal.name.length === 0) {
        return {
          itype: 'is-cblue-4',
          imessage: ''
        }
      } else if (this.showmodal.name.length > 0 && this.showmodal.name.length < 2) {
        return {
          itype: 'is-cred-1',
          imessage: '名称长度小于2'
        }
      } else {
        return {
          itype: 'is-cgreen-2',
          imessage: ''
        }
      }
    },
    retrunActiveRelease() {
      return this.$store.state.releasemanagement.activeRelease
    }
  },
  watch: {
    retrunActiveRelease: {
      immediate: true,
      handler(val) {
        this.activerelease = val
      },
      deep: true
    }
  },
  mounted() {},

  methods: {
    /**
     * 创建新的释放任务
     */
    createNewRelTask() {
      this.showmodal.isshow = true
    },
    /**
     * 导入释放任务
     */
    importRelTask() {},
    /**
     * 关闭modal
     */
    closeModal() {
      this.$store.commit('initActiveRelease', this.showmodal)
      this.defaultshow = true
      // 初始化
      this.showmodal = {
        isshow: false,
        name: '',
        assigner: '',
        createautor: ''
      }
    },
    /**
     * 打开活动的
     */
    openActive() {
      const that = this
      this.defaultshow = true
      console.log(this.$refs)
      setTimeout(() => {
        that.$refs.releaseentrance.getReleaseTask()
      }, 100)
    }
  }
}
</script>
