<template>
  <section>
    <b-message class="is-cblue-3 notification message padding-01" :closable="false">
      <div class="columns">
        <div class="column is-2">
          <b-field label="产品类别:">
            <b-select v-model="localbasic.product" placeholder="产品类别" expanded>
              <option>Scale Manager</option>
              <option>MTStar(JAVA)</option>
            </b-select>
          </b-field>
          <b-field label="产品类型:">
            <b-select v-model="localbasic.type" placeholder="产品类型" expanded>
              <option>Store</option>
              <option>Store-License</option>
              <option>HQ-License</option>
              <option>Multi-Store</option>
              <option>Multi-Store-License</option>
            </b-select>
          </b-field>
          <b-field label="是否包含Update包:">
            <b-select v-model="localbasic.uppack" placeholder="Update包" expanded>
              <option :value="true">包含</option>
              <option :value="false">不包含</option>
            </b-select>
          </b-field>
          <b-field label="产品年度释放项:">
            <b-input v-model="localbasic.relphase" />
          </b-field>
        </div>
        <div class="column is-2">
          <b-field label="预期释放版本号:">
            <b-input v-model="localbasic.relversion" />
          </b-field>
          <b-field label="高级配置SVN号:">
            <b-input v-model="localbasic.configsvn" />
          </b-field>
          <b-field label="预期释放时间:" grouped>
            <b-datepicker v-model="texpectdate" position="is-top-right" @input="selectedExpectDate">
              <template v-slot:trigger>
                <b-button icon-left="calendar-today" type="is-cwhite-1" />
              </template>
            </b-datepicker>
            <b-input v-model="localbasic.expectdate" expanded readonly />
          </b-field>
          <b-field label="实际释放时间:" grouped>
            <b-datepicker v-model="tacutaldate" position="is-top-right" @input="selectedActualDate">
              <template v-slot:trigger>
                <b-button icon-left="calendar-today" type="is-cwhite-1" />
              </template>
            </b-datepicker>
            <b-input v-model="localbasic.acutaldate" expanded readonly />
          </b-field>
        </div>
        <div class="column is-3">
          <div class="is-cwhite-1 notification padding-05" :closable="false">
            <div class="level">
              <div class="level-left">
                <div class="level-item">
                  <p class="subtitle">需求&用例</p>
                </div>
              </div>
              <div class="level-right">
                <div class="level-item">
                  <b-button size="is-small" type="is-cgreen-4">导入</b-button>
                </div>
              </div>
            </div>
            <hr class="margin-05" />
            <div>
              <b-field class="padding-01 margin-0" grouped>
                <b-select v-model="edittrcase.type" placeholder="TR&Case" size="is-small">
                  <option>TR</option>
                  <option>Case</option>
                </b-select>
                <b-input v-model="edittrcase.id" placeholder="编号" size="is-small"></b-input>
              </b-field>
              <b-field>
                <b-input v-model="edittrcase.value" placeholder="标题" size="is-small" expanded></b-input>
                <b-icon icon="plus" class=" pl-4 pt-1" type="is-danger" @click.native="addTrCase()"></b-icon>
              </b-field>
            </div>
            <hr class="margin-05" />
            <div>
              <div v-for="(item, index) in localtrcase" :key="index">
                <p class="is-italic">
                  <b-tag size="is-cblue-2">{{ item.id }}</b-tag>
                  {{ item.value }}
                </p>
              </div>
            </div>
          </div>
        </div>
        <div class="column is-5">
          <div class="is-cwhite-1 notification padding-05" :closable="false">
            <div class="level">
              <div class="level-left">
                <div class="level-item">
                  <p class="subtitle">更新详情</p>
                </div>
              </div>
              <div class="level-right">
                <div class="level-item">
                  <b-button size="is-small" type="is-cyellow-1">导入</b-button>
                </div>
              </div>
            </div>
            <hr class="margin-05" />
            <div>
              <b-field>
                <b-input v-model="localemail" maxlength="1200" type="textarea"></b-input>
              </b-field>
            </div>
          </div>
        </div>
      </div>
    </b-message>
  </section>
</template>

<script>
export default {
  name: 'RelBasic',
  components: {},
  props: {
    info: Object
  },
  data() {
    return {
      /**
       * 待返回给父节点的值
       * 基本
       * 本地
       */
      localbasic: {},
      localtrcase: [],
      localemail: '',
      texpectdate: null,
      tacutaldate: null,
      /**
       * 可被编辑的trcase框
       */
      edittrcase: {
        type: '',
        id: '',
        value: ''
      }
    }
  },
  computed: {},
  watch: {
    info: {
      immediate: false,
      handler(val) {
        this.localbasic = val.basic
        this.localtrcase = val.trcase
        this.localemail = val.email.email
      },
      deep: true
    },
    localbasic: {
      immediate: false,
      handler(val) {
        // 组件内对值变更后向外部发送事件通知
        this.$store.commit('updateActiveRelease', {
          type: 'basic',
          value: val
        })
      },
      deep: true
    },
    localtrcase: {
      immediate: false,
      handler(val) {
        // 组件内对值变更后向外部发送事件通知
        this.$store.commit('updateActiveRelease', {
          type: 'trcase',
          value: val
        })
      },
      deep: true
    },
    localemail: {
      immediate: false,
      handler(val) {
        // console.log('更新')
        // 组件内对值变更后向外部发送事件通知
        this.$store.commit('updateActiveRelease', {
          type: 'email',
          value: val
        })
      },
      deep: true
    }
  },
  methods: {
    selectedExpectDate(value) {
      this.localbasic.expectdate = value.toISOString().split('T')[0]
    },
    selectedActualDate(value) {
      this.localbasic.acutaldate = value.toISOString().split('T')[0]
    },

    /**
     * 添加信息的
     */
    addTrCase() {
      this.localtrcase.push(this.edittrcase)
      // 初始化
      this.edittrcase = {
        type: '',
        id: '',
        value: ''
      }
    }
  }
}
</script>

<style scoped>
.padding-05 {
  padding: 0.5rem;
}

.padding-01 {
  padding: 0.1rem;
}

.margin-0 {
  margin: 0rem;
}

.margin-05 {
  margin: 0.5rem;
}
</style>
