<template>
  <section>
    <b-table
      size="is-small"
      :data="localcheckpoint"
      :columns="tablecolumn"
      :checked-rows.sync="checkedRows"
      :current-page.sync="currentpage"
      checkable
      paginated
      :narrowed="true"
      per-page="8"
      class="msg"
    />
  </section>
</template>

<script>
export default {
  name: 'RelTest',
  props: {},
  data() {
    return {
      origin: '',
      /**
       * 已勾选的checkbox行
       * 待返回给父节点的值
       * test
       */
      localcheckpoint: [],
      checkedRows: [],
      // 测试内容的行名称
      tablecolumn: [
        {
          field: 'value',
          label: '测试点描述'
        }
      ],
      /**
       * 默认起始页
       */
      currentpage: 1
    }
  },

  watch: {
    origin: {
      immediate: false,
      handler(val) {
        // 执行方法
        if (val !== '' && val !== undefined) {
          this.init(val)
        }
      },
      deep: true
    },
    checkedRows: {
      immediate: false,
      handler(val) {
        // 组件内对值变更后向外部发送事件通知
        this.$store.commit('updateActiveRelease', {
          type: 'dest',
          value: val
        })
      },
      deep: true
    }
  },
  mounted() {},
  methods: {
    /**
     * 初始化从email获得的数据,转置格式
     */
    init(val) {
      // const newp = val.split(/[(\r\n)\r\n]+/) // 按行分拆
      const newp = val.split('\n') // 按行分拆
      this.localcheckpoint = this.assistData(newp) // 处理
      // 组件内对值变更后向外部发送事件通知
      this.$store.commit('updateActiveRelease', {
        type: 'origin',
        value: this.localcheckpoint
      })
    },
    /**
     * 处理数据
     * @param {Array} data 依靠split换行形成的数组，直接使用
     * 用这个方法
     */
    assistData(data) {
      // console.log(data)
      const re1 = /[0-9]\)/
      const re2 = /[a-z]\)/
      const temp = []
      for (let index = 0; index < data.length; index++) {
        const element = data[index]
        /**
         * 包含为 0, 不包含为 -1
         */
        if (re1.test(element) === false && re2.test(element) === false) {
          // 全不包含, 父,单个更新点的主节点
          // console.log('父')
          temp.push({
            value: element,
            index: index
          })
        } else if (re1.test(element) === true) {
          // 包含[0-9])子
          // console.log('子')
          temp.slice(-1)[0].value += '\n&nbsp;&nbsp;' + element
        } else {
          // 包含[a-z])孙
          // console.log('孙')
          temp.slice(-1)[0].value += '\n&nbsp;&nbsp;&nbsp;&nbsp;' + element
        }
      }
      return temp // 返回值
    },

    /**
     * 设置Origin数据
     */
    setOrigin() {
      this.origin = this.$store.state.releasemanagement.activeRelease.email.email
    }
  }
}
</script>

<style scoped>
.msg {
  white-space: pre-wrap;
  /*这是重点。文本换行*/
}

.padding-05 {
  padding: 0.5rem;
}
</style>
