<template>
  <section>
    <b-message class="is-cblue-3 notification message" :closable="false">
      <div class="columns">
        <div class="column is-8">
          <form>
            <b-field label="测试注意点">
              <b-checkbox v-model="localtest.version" type="is-success" size="is-small">
                版本号验证
              </b-checkbox>
              <b-checkbox v-model="localtest.new" type="is-success" size="is-small">
                新功能验证
              </b-checkbox>
              <b-checkbox v-model="localtest.improve" type="is-success" size="is-small">
                改进功能验证
              </b-checkbox>
              <b-checkbox v-model="localtest.fix" type="is-success" size="is-small">
                修复功能验证
              </b-checkbox>
              <b-checkbox v-model="localtest.cod" type="is-success" size="is-small">
                COD功能验证
              </b-checkbox>
              <b-checkbox v-model="localtest.updatepackage" type="is-success" size="is-small">
                升级包功能验证
              </b-checkbox>
              <b-checkbox v-model="localtest.license" type="is-success" size="is-small">
                注册功能验证
              </b-checkbox>
              <b-checkbox v-model="localtest.regressiontest" type="is-success" size="is-small">
                回归测试验证
              </b-checkbox>
            </b-field>
            <b-field label="版本号" grouped>
              <b-input v-model="tempversionno.self" size="is-small" placeholder="本体版本"></b-input>
              <b-input v-model="tempversionno.cod" size="is-small" placeholder="COD版本"></b-input>
              <b-input v-model="tempversionno.sk" size="is-small" placeholder="ScaleKey版本"></b-input>
            </b-field>
          </form>
          <form>
            <b-field label="释放前文档检查">
              <b-checkbox v-model="localdoc.devlopreleasemail" type="is-success" size="is-small">
                开发释放邮件
              </b-checkbox>
              <b-checkbox v-model="localdoc.testplan" type="is-success" size="is-small">
                测试计划文档
              </b-checkbox>
              <b-checkbox v-model="localdoc.releaseplan" type="is-success" size="is-small">
                正式释放文档
              </b-checkbox>
              <b-checkbox v-model="localdoc.use360" type="is-success" size="is-small">
                是否提交360验证
              </b-checkbox>
              <b-checkbox v-model="localdoc.automatic" type="is-success" size="is-small">
                是否包含自动化测试
              </b-checkbox>
            </b-field>
            <div class="columns">
              <div class="column is-6">
                <b-field label="软件证书号">
                  <b-input v-model="localdoc.certificate" size="is-small" placeholder="certificate"></b-input>
                </b-field>
                <b-field label="软件列表文档路径">
                  <b-input v-model="localdoc.swlistpath" size="is-small" placeholder="swlistpath"></b-input>
                </b-field>
                <b-field label="更新点文档路径">
                  <b-input v-model="localdoc.whatsnewpath" size="is-small" placeholder="whatsnewpath"></b-input>
                </b-field>
                <b-field label="目标代码释放路径">
                  <b-input v-model="localdoc.targetreleasepath" size="is-small" placeholder="whatsnewpath"></b-input>
                </b-field>
              </div>
            </div>
          </form>
        </div>
      </div>
    </b-message>
  </section>
</template>

<script>
export default {
  name: 'RelDoc',
  props: {},
  data() {
    return {
      localtest: {},
      localdoc: {},
      test: {},
      doc: {},

      tempversionno: {
        self: '',
        cod: '',
        sk: ''
      }
    }
  },
  watch: {
    test: {
      immediate: false,
      handler(val) {
        this.localtest = val
      },
      deep: true
    },
    doc: {
      immediate: false,
      handler(val) {
        this.localdoc = val
      },
      deep: true
    },
    tempversionno: {
      immediate: false,
      handler(val) {
        this.localtest.versionlist = [val.self, val.cod, val.sk]
      },
      deep: true
    },
    localtest: {
      immediate: false,
      handler(val) {
        // 组件内对值变更后向外部发送事件通知
        this.$store.commit('updateActiveRelease', {
          type: 'test',
          value: val
        })
      },
      deep: true
    },
    localdoc: {
      immediate: false,
      handler(val) {
        // 组件内对值变更后向外部发送事件通知
        this.$store.commit('updateActiveRelease', {
          type: 'doc',
          value: val
        })
      },
      deep: true
    }
  },
  methods: {
    /**
     * 获取test/doc
     */
    setTestDoc() {
      this.localtest = this.$store.state.releasemanagement.activeRelease.test
      this.localdoc = this.$store.state.releasemanagement.activeRelease.doc
    }
  }
}
</script>

<style scoped>
.padding-05 {
  padding: 0.5rem;
}
</style>
