<template>
  <div>
    <div class="card padding-05">
      <div class="card-content padding-05">
        <b-message :title="`关联任务信息` + activetask.taskname" type="is-cblue-3" :closable="false">
          <div class="columns content is-small">
            <div v-for="(item, index) in returnBasicInfo()" :key="index" :value="item" class="column">
              <h4>{{ item.label }}</h4>
              <strong>{{ item.value }}</strong>
            </div>
          </div>
        </b-message>
        <b-steps :has-navigation="false" :rounded="false" size="is-small" @input="changeactivestep">
          <b-step-item v-bind="defstep[0]">
            <rel-basic ref="basic" :info="activetask" />
          </b-step-item>
          <b-step-item v-bind="defstep[1]">
            <rel-test ref="test" />
          </b-step-item>
          <b-step-item v-bind="defstep[2]">
            <rel-doc ref="doc" />
          </b-step-item>
          <b-step-item v-bind="defstep[3]">
            <rel-check ref="check" />
          </b-step-item>
        </b-steps>
      </div>
    </div>
  </div>
</template>

<script>
import RelBasic from '@/components/ReleaseManagement/ReleaseStep/RelBasic'
import RelTest from '@/components/ReleaseManagement/ReleaseStep/RelTest'
import RelDoc from '@/components/ReleaseManagement/ReleaseStep/RelDoc'
import RelCheck from '@/components/ReleaseManagement/ReleaseStep/Check'

export default {
  name: 'ReleaseEntrance',
  components: {
    RelBasic,
    RelTest,
    RelDoc,
    RelCheck
  },
  data() {
    return {
      /**
       * 活动任务
       */
      activetask: {},

      /**
       * 检查进度
       * 初始15%
       */
      checkprogress: 15,

      /**
       * 定义各个步骤的属性
       */
      defstep: [
        {
          step: 1,
          label: '基本信息',
          icon: 'video-input-component',
          value: '1',
          clickable: true,
          type: {
            'is-success': true
          }
        },
        {
          step: 2,
          label: '测试内容',
          icon: 'test-tube',
          value: '2',
          clickable: true,
          type: {
            'is-success': true
          }
        },
        {
          step: 3,
          label: '释放文档准备',
          icon: 'file-document',
          value: '3',
          clickable: true,
          type: {
            'is-success': true
          }
        },
        {
          step: 4,
          label: '最后检查',
          icon: 'checkbox-multiple-marked',
          value: '4',
          clickable: true,
          type: {
            'is-success': true
          }
        }
      ]
    }
  },
  computed: {
    titleStack() {
      return ['RoWalock', '当前释放版本']
    }
  },
  mounted() {
    this.getReleaseTask()
  },

  methods: {
    /**
     * 获取最新的释放任务
     */
    getReleaseTask() {
      // TODO: 这个地方应该是通过访问后端获取的
      // test
      this.activetask = this.$store.state.releasemanagement.activeRelease
    },
    /**
     * 更改活动页的操作
     */
    changeactivestep(value) {
      if (value === '2') {
        this.$refs.test.setOrigin()
      }
      if (value === '3') {
        this.$refs.doc.setTestDoc()
      }
      if (value === '3') {
        this.$refs.check.setData()
      }
      const numbervalue = Number(value)
      this.checkprogress = numbervalue * 15
    },
    /**
     * 返回基本信息,显示在第一个message内
     */
    returnBasicInfo() {
      return [
        {
          label: '任务 id',
          value: this.activetask.taskid
        },
        {
          label: '创建时间',
          value: this.activetask.createdate
        },
        {
          label: '创建者',
          value: this.activetask.createautor
        },
        {
          label: '执行人',
          value: this.activetask.assigner
        }
      ]
    }
  }
}
</script>

<style scoped>
.margin-top-bottom >>> progress {
  margin-top: 0.75rem;
  margin-bottom: 0.75rem;
}

.margin-top-bottom >>> p {
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
}

.padding-05 {
  padding: 0.5rem;
}
</style>
